import React from 'react';
import { connect } from 'react-redux';
import { withStyles } from '@material-ui/core/styles';
import CardContent from '@material-ui/core/CardContent';
import Card from '@material-ui/core/Card';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import SearchIcon from '@material-ui/icons/Search';
import Alert from '@material-ui/lab/Alert';
import ContractSelector from '../components/ContractSelector';
import TransactionResultTable from '../components/TransactionResultTable';
import EventResultTable from '../components/EventResultTable';
import { selectContractAction } from '../redux/actions/contractActions';
import { GetContractCreationTx, GetEvents, GetInternalToTxs, GetToTxs } from '../constants';
import { getContractCreationTx, getInternalToTxs, getToTxs, getEvents } from '../client/fetcher';

const styles = {
    card: {
        minWidth: 275,
        marginTop: 5,
        marginBottom: 5,
    },
};

const pageSize = 10;

const actions = [GetContractCreationTx, GetToTxs, GetInternalToTxs, GetEvents];

class ContractInfoContainer extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            selectedAction: "",
            displayTxResult: false,
            displayEventResult: false,
            isLoading: true,
            displayData: [],
            currentPage: 0,
            errorMessage: "",
        }
    }

    componentWillUnmount() {
        this.props.dispatch(selectContractAction(""))
    }

    handleSelectedContractChange = (e) => {
        this.setState({ errorMessage: ""});
        this.props.dispatch(selectContractAction(e.target.value))
    };

    handleSelectedActionChange = (e) => {
        this.setState({
            errorMessage: "",
            selectedAction: e.target.value,
        })
    };

    handleChangePage = (event, newPage) => {
        this.setState({currentPage: newPage})
    };

    handleSearch = () => {
        // TODO: restrict by page
        this.setState({
            displayTxResult: false,
            displayEventResult: false,
        });
        if (this.state.selectedAction === ""){
            this.setState({ errorMessage: "no action selected"});
            return
        }
        if (this.props.selectedContract === "") {
            this.setState({ errorMessage: "no contract selected"});
            return
        }
        this.setState({
            isLoading: true,
            errorMessage: "",
        });
        if (this.state.selectedAction === GetEvents) {
            getEvents(this.props.rpcEndpoint, this.props.selectedContract).then( (res) => {
                this.setState({
                    displayData: res,
                    isLoading: false,
                })
            }).catch( (e) => {
                this.setState({
                    displayData: [],
                    isLoading: false,
                    errorMessage: e.toString(),
                })
            });
            this.setState({displayEventResult: true});
        } else {
            switch (this.state.selectedAction) {
                case GetContractCreationTx:
                    getContractCreationTx(this.props.rpcEndpoint, this.props.selectedContract).then( (res) => {
                        this.setState({
                            displayData: [res],
                            isLoading: false,
                        })
                    }).catch( (e) => {
                        console.log(e);
                        this.setState({
                            displayData: [],
                            isLoading: false,
                            errorMessage: e.toString(),
                        })
                    });
                    break;
                case GetToTxs:
                    getToTxs(this.props.rpcEndpoint, this.props.selectedContract).then( (res) => {
                        this.setState({
                            displayData: res,
                            isLoading: false,
                        })
                    }).catch( (e) => {
                        this.setState({
                            displayData: [],
                            isLoading: false,
                            errorMessage: e.toString(),
                        })
                    });
                    break;
                case GetInternalToTxs:
                    getInternalToTxs(this.props.rpcEndpoint, this.props.selectedContract).then( (res) => {
                        this.setState({
                            displayData: res,
                            isLoading: false,
                        })
                    }).catch( (e) => {
                        this.setState({
                            displayData: [],
                            isLoading: false,
                            errorMessage: e.toString(),
                        })
                    });
                    break;
                default:
                    this.setState({
                        displayData: [],
                        isLoading: false,
                        errorMessage: "unknown action",
                    })
            }
            this.setState({displayTxResult: true});
        }
    };

    render(){
        return (
            <Card className={this.props.classes.card}>
                <CardContent>
                    <div align="center">
                        <Typography variant="h6">
                            Select Contract
                        </Typography>
                        <br/>
                        <ContractSelector
                            selectedContract={this.props.selectedContract}
                            contracts={this.props.contracts}
                            handleSelectedContractChange={this.handleSelectedContractChange}
                            actions={actions}
                            selectedAction={this.state.selectedAction}
                            handleSelectedActionChange={this.handleSelectedActionChange}
                        />
                        <br/>
                        <Button variant="contained" color="primary" onClick={this.handleSearch}>
                            <SearchIcon />
                            &nbsp;
                            Search
                        </Button>
                    </div>
                    <br/>
                    {
                        this.state.errorMessage &&
                        <div>
                            <br/>
                            <Alert severity="error">{this.state.errorMessage}</Alert>
                        </div>
                    }
                    {
                        this.state.displayTxResult &&
                        <TransactionResultTable
                            displayData={this.state.displayData}
                            isLoading={this.state.isLoading}
                            currentPage={this.state.currentPage}
                            pageSize={pageSize}
                            totalTxs={this.state.displayData.length} // TODO: fetch number of total txs separately
                            handleChangePage={this.handleChangePage}
                        />
                    }
                    {
                        this.state.displayEventResult &&
                        <EventResultTable
                            displayData={this.state.displayData}
                            isLoading={this.state.isLoading}
                            currentPage={this.state.currentPage}
                            pageSize={pageSize}
                            totalEvents={this.state.displayData.length} // TODO: fetch number of total events separately
                            handleChangePage={this.handleChangePage}
                        />
                    }
                </CardContent>
            </Card>
        )
    }
}

const mapStateToProps = state => {
    return {
        rpcEndpoint: state.system.rpcEndpoint,
        contracts: state.user.contracts,
        selectedContract: state.user.selectedContract,
    }
};

export default connect(mapStateToProps)(withStyles(styles)(ContractInfoContainer))